<template>
  <div id="app">
    <!-- <img src="./assets/logo.png">
    <router-view/> -->    
    <br>
    <select v-model='SelectName' @change="NameData">
			<option v-for="(item, index) in AvailableValues" :key="index" :value="item">{{item}}</option>
		</select>
    <br>
    <!-- {{this.info.records.location}} -->
    <div v-if="info">
      <div v-for="(item, index) in this.info" :key="index" :value="item">
        <div v-for="(kk,index) in item.location">
          <!-- {{index}} -->
          地區:{{kk.locationName}}
          <br>
          {{kk.weatherElement[2].time[0].parameter.parameterName}}~{{kk.weatherElement[2].time[2].parameter.parameterName}}
          <br>
          降雨機率:{{kk.weatherElement[1].time[0].parameter.parameterName}}%
          <br>
          {{kk.weatherElement[0].time[0].parameter.parameterName}}</div>
      </div>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'App' ,
  data: function () {
    return {
      count: 0,
      info:null,
      AvailableValues:['','宜蘭縣','花蓮縣','臺東縣','澎湖縣','金門縣','連江縣','臺北市','新北市','桃園市','臺中市','臺南市','高雄市','基隆市','新竹縣','新竹市','苗栗縣','彰化縣','南投縣','雲林縣','嘉義縣','嘉義市','屏東縣'],
      SelectName:''
    }
  },
  methods:{NameData:function(){
    const cors = 'https://cors-anywhere.herokuapp.com/';
    let url = 'https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-C0032-001?Authorization=CWB-00B20C26-CA71-4ACD-A11B-EFFB57D3E668&locationName='+ this.SelectName;
    this.$axios
      .get(cors+url)
      .then(response => {
        console.log(response)
        this.info = response.data
      })
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



